import React from "react";
import { render } from "react-dom";
import "./styles.css";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      count: 0
    };
  }
  incrementCount() {
    this.setState({ count: this.state.count + 1 });
  }
  decrementCount() {
    this.setState({ count: this.state.count - 1 });
  }

  render() {
    return (
      <div className="App">
        <h1>Hello Counter</h1>
        <h3>{this.state.count}</h3>
        <button
          onClick={() => {
            this.incrementCount();
          }}
        >
          +
        </button>
        <button
          onClick={() => {
            this.decrementCount();
          }}
        >
          -
        </button>
      </div>
    );
  }
}
export default App;
